body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-size: 15px;
  
}

.header {
  background-color:#CCC;
  min-height: 65px;
  margin-bottom: 15px;
  padding: 10px;
}

.header-options {
  padding: 15px; 
  font-size: 30px; 
}
.spaceDivider{
  width:15px;
  height: auto;
  display:inline-block;
}
.quantityDivider{
  width:0px;
  height: auto;
  display:inline-block;
}
.heightDivider{
  height:15px;
}
.quantitySelectorDiv{
display: inline-block;
}
.quantityInput{
  width:15%;
}
.quantityMinusButton{
  position:"top-right";

}
.modal{
  position: fixed;
  z-index:1;
  left:0;
  top:0;
  width:100%;
  height:100%;
  overflow: auto;
  background-color:rgb(0,0,0);
  background-color: rgba(0,0,0,4);
}
.modal-header{
  text-align: center;
  font-size: 40 px;
}
.modal-footer{
  text-align: center;
}
.modal-content{
  background-color: #fefefe;
  margin:15% auto;
  padding:20px;
  border: 1 px solid #888;
  width:65%;
  font-size: 20px;
  position: relative;
  -webkit-animation-name: animatetop;
          animation-name: animatetop;
  -webkit-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);
          box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19); 
  -webkit-animation-duration: 0.4s; 
          animation-duration: 0.4s;
}
@-webkit-keyframes animatetop {
  from {top: -300px; opacity:0}
  to {top:0; opacity:1}
}
@keyframes animatetop {
  from {top: -300px; opacity:0}
  to {top:0; opacity:1}
}
.header-menu {
  float: right;
  /* width: 150px;   */
}

input.big{
  font-size: 30px;
} 
input.pin { width: 70px;} 

input.big-text {
  font-size: 30px;
  width: 100%;
}

input.customer-name-input {  
  font-size: 30px;
  width: 300px;
 }
.paid-date input {
  font-size: 20px; width: 400px;
}
.paid-date button {
  font-size: 20px;
  margin-left: 10px;
}

.open-orders {
  margin: 10px;  
}

.open-order-card .time {
  font-size: 11px;
  color: darkblue;
  margin-top: 5px;
}
.open-order-card .customer-name {  
  color: darkblue;
  font-size: 25px;
  margin-bottom: 5px;
}
.pin-entry {
  border: 1px solid black;
  border-radius: 8px;
  margin: 0 10px 0 10px;
  display: inline-block;
  
  text-align: center;
  font-size: 18px;
  padding: 0 10px 0 10px;
}
.pin-entry input { font-size: 25px;}
.search-input{
  width: 75%;
  font-size: 20px; 
  -webkit-box-sizing: border-box; 
          box-sizing: border-box;
  background-color:white;
  height:55%;
}
.card {
  border: 0px;
  border-radius: 8px;
  padding: 10px; 
  margin: 10px;
  background-color: lightblue;
  display: inline-block;
  
  text-align: center;
  cursor: pointer;
  font-size: 18px;
}

.quiet-card {
  background-color: #888;
  color: #444;
}
.wide-card {
  width: 100px;
}
.small-card {
  padding: 5px;
  margin: 5px;
  font-size: 13px;
}
.smallItems-card {
 border-radius:35%;
 height: 5;
 width:10;
 margin-right: 10%;
 -ms-flex-pack: center;
     justify-content: center;
 font-size: 14px;
}
.danger-card { 
  background-color: darkred;
  color: #DDDDDD;
}
.order .right-side {
  width: 425px;
  background-color: #BBBBBB;
  float: right;
  padding: 10px;
}

.back-button-card {
  background-color: blueviolet;
  color: white; 
}

.order-items table { width: 100%; border-collapse: collapse; text-align: left;}
.order-items table tbody td { height: 30px; padding-left: 5px; padding-right: 5px;}
.order-items table tfoot th { height: 30px; text-align: right; padding-right: 10px; }
.order-items table tfoot td { width: 75px; }
.order-items table tfoot tr.divider td { border-top: 2px solid black; }
.order-items table tfoot tr.divider th { border-top: 2px solid black; }
.order-items td.quantity { width: 125px; }

.product-card {
  background-color: goldenrod;
  
}
.tag-card {
  background-color: cornflowerblue;
  color: white;
}

.order-header {
  background-color: #CCC;
  padding: 15px;
  height: 40px;
}
.order-header .customer-name {
  font-size: 30px;
  width: 350px;
}
.order-actions{
  float: right;
}
.order-actions .card { margin-top:0px; margin-bottom: 0px;}

.save-button-card{
