  margin-bottom: 0px;
}
table.order-list {
  width: 100%;
}

.valid { color: darkgreen; }
.invalid {color: darkred;}

table.admin-table {
  width: 100%;
  border-collapse: collapse;
  text-align: left;
}

table.admin-table thead th {
  background-color: #BBBBBB;
  padding: 5px 0 5px 0;
}

